.titleBar {
  display: flex;
  justify-content: center;
  align-items: center;
  position: fixed;
  background: #fff;
  width: 100%;
  height: 23px;
  z-index: 99;
  -webkit-app-region: drag;
  -webkit-user-select: none;
}

.titleBarBlank {
  height: 23px;
  width: 100%;
}



